'use strict'
import React, {PropTypes} from 'react';


const PDFEmbed = ({source}) => {
  return (
    <div className="flex center width100 pdf-container">
      <object data={source} type="application/pdf" width="100%" height="100%">
        <iframe src={source} width="100%" height="100%" style={{border: "none"}}>
        This browser does not support PDFs. Please download the PDF to view it: <a href={source}>Download PDF</a>
        </iframe>
      </object>
    </div>
  );
};

PDFEmbed.propTypes = {
  source: PropTypes.string
};


export default PDFEmbed;



