'use strict'
import React, {PropTypes} from 'react';


const WebEmbed = ({source}) => {
  return (
  	<div className="flex center width100">
			<iframe className="width100" style={{height: "600px"}} src={source}></iframe>
		</div>
  );
};

WebEmbed.propTypes = {
  source: PropTypes.array
};


export default WebEmbed;


