'use strict'
import React, {PropTypes} from 'react';
import videoPlayer from '../../services/videoPlayer';

// import 'https://www.youtube.com/iframe_api?noext'

class VideoEmbed extends React.Component {
  componentDidMount(){
    const {data} = this.props;
    let player = new videoPlayer(this.props.data)
    player.createControls()
  }
  render() {
    const {data} = this.props;
    return (
      <div width="560px" height="315px" style={{display: "inline-block", position: "relative", height: "315px", width: "560px"}} >
        <div width="560px" height="315px" style={{backgroundImage: "url(https://i.ytimg.com/vi/" + data.id + "/hqdefault.jpg)", backgroundRepeat:"no-repeat", backgroundSize: "cover", backgroundPosition: "center", height: "315px", width: "560px", position: "absolute"}} id={`vid${data.id}`} data-video-id={data.id} >
        </div>
      </div>
    );
  }
}

VideoEmbed.propTypes = {
  data: PropTypes.object
};


export default VideoEmbed;



