const ProgressBar = props => (
  <div style={{alignSelf: "flex-end", position: "absolute", width: "100%", height: "9px"}}>
    <div 
      onClick={props.clickProgress} 
      className="" 
      style={{ cursor: "pointer", pointerEvents: "initial", position: "absolute", width: "100%", height: "9px"}} 
      ref={(c) => props.setProgressBar(c)} >
      <div className="width100">
        <div style={{backgroundColor: "#AB0015", width: props.progress + "%", height: "5px", marginTop: "3px"}}>
        </div>
      </div>
    </div>
    <div 
      className="flex" 
      style={{ cursor: "pointer", alignSelf: "flex-end", pointerEvents: "none", position: "absolute", borderTop: "1px solid rgba(255,255,255,0.5)", width: "100%", height: "9px"}}>
      <div className="width100" style={{position: "relative"}}>
      {
          props.media.map((x) => {
            if(props.duration) {
              let margin = (x.start/props.duration) * 100;
              return <div onMouseOver={() => {props.mouseOverMedia(x)}}  onMouseOut={props.mouseOutMedia} onClick={() => {props.clickMedia(x)}} style={{marginLeft: `${margin}%`, position: "absolute", backgroundColor: "#959D9B", width: "13px", height: "13px", pointerEvents: "initial"}} className="pointer prgrss-tooltip"></div>
            }
          })
      }
      </div>
    </div>
  </div>
)


