'use strict'
import React, {PropTypes} from 'react';

class SVG extends React.Component {
  componentDidMount(){
  }
  render() {
    const {children, style} = this.props;
    return (
      <svg style={style}>
        {children}
      </svg>
    );
  }
}

SVG.propTypes = {
  children: PropTypes.object,
  style: PropTypes.object
};

export default SVG;



