'use strict'
import React, {PropTypes} from 'react';


function polarToCartesian(centerX, centerY, radius, angleInDegrees) {
  var angleInRadians = (angleInDegrees-90) * Math.PI / 180.0;

  return {
    x: centerX + (radius * Math.cos(angleInRadians)),
    y: centerY + (radius * Math.sin(angleInRadians))
  };
}

function describeArc(x, y, radius, startAngle, endAngle){

  var start = polarToCartesian(x, y, radius, endAngle);
  var end = polarToCartesian(x, y, radius, startAngle);

  var arcSweep = endAngle - startAngle <= 180 ? "0" : "1";

  var d = [
      "M", start.x, start.y, 
      "A", radius, radius, 0, arcSweep, 0, end.x, end.y
  ].join(" ");

  return d;       
}


class SvgVideoController extends React.Component {
  render() {
    const {data, height, width, hover, play, mouseOut, playAt} = this.props;
    const partsNo = data.parts.length;
    const degrees = 360/partsNo;
    let startDegrees = 0;
    const pathStyle = {
      stroke: "rgb(243, 253, 255)",
      strokeWidth: 3,
      fill: "none"
    }
    const outerPathStyle = {
      stroke: "rgb(243, 253, 255)",
      strokeWidth: 1,
      fill: "none"
    }
    const centerPathStyle = {
      stroke: "rgb(243, 253, 255)",
      strokeWidth: 20,
      opacity: 0.3,
      fill: "none"
    }
    const outerCircleStyle = {
      stroke: "rgb(243, 253, 255)",
      strokeWidth: 8,
      opacity: 0.5,
      fill: "none"
    }
    const outerCircleStyle2 = {
      stroke: "rgb(243, 253, 255)",
      strokeWidth: 5,
      fill: "none"
    }
    const radius = parseInt(height)/3;
    const slices = data.parts.map((part, i) => {
            let start = startDegrees + 2;
            let end = (degrees * (i + 1)) - 2;
            let path = (<Slice 
                        d={describeArc(parseInt(width)/2, parseInt(height)/2, (radius) -4, start, end)} 
                        style={pathStyle} 
                        outerD={describeArc(parseInt(width)/2, parseInt(height)/2, (radius + 12), start, end)}
                        outerStyle={outerPathStyle} 
                        centerD={describeArc(parseInt(width)/2, parseInt(height)/2, (radius+2), start, end)}
                        centerStyle={centerPathStyle} 
                        hover={hover}
                        mouseOut={mouseOut}
                        part={part}
                        playAt={playAt}/>)
            startDegrees += degrees
            return path
          })
    return (
      <g>
        {
        // <circle cx={parseInt(width)/2} cy={parseInt(height)/2} r={(radius)+20} stroke="" stroke-width="3" fill="rgba(255,255,255,0.1)" />
        // <path d={describeArc(parseInt(width)/2, parseInt(height)/2, (radius) + 4, 0, 359.999)} style={outerCircleStyle}></path>
        }
        {
          slices
        }
        {
        <polygon onClick={play} points={`${parseInt(width)/2+15},${parseInt(height)/2}, ${parseInt(width)/2-15},${parseInt(height)/2+15} ${parseInt(width)/2-15},${parseInt(height)/2-15}`} style={{fill: "rgba(255,255,255,0.5)", strokeWidth: 2}}></polygon>
        }
      </g>
    );
  }
}

SvgVideoController.propTypes = {
  data: PropTypes.object,
  height: PropTypes.string,
  width: PropTypes.string
};


export default SvgVideoController;


class Slice extends React.Component {
  constructor(props) {
    super(props);
    this.state = {focused: false};
    this.onMouseEnter = this.onMouseEnter.bind(this);
    this.onMouseLeave = this.onMouseLeave.bind(this);
    this.play = this.play.bind(this);
  }
  onMouseEnter(){
    console.log("MOUSE ENTER", this.props.part);
    // stroke: "rgba(243, 253, 255, 0.0)",
    this.props.hover(this.props.part)
    this.setState({focused: true})
  }
  onMouseLeave(){
    this.props.mouseOut()
    this.setState({focused: false})
    console.log("this.state", this.state);
  }
  play(){
    this.props.playAt(this.props.part.start)
  }
  render() {
    const {d, style, centerD, centerStyle, outerD, outerStyle, playAt} = this.props;
    const className = this.state.focused ? "focused" : "";
    return (
      <g onMouseEnter={this.onMouseEnter} onMouseLeave={this.onMouseLeave} onClick={this.play}>
        {
          // <path d={d} style={style} ></path>
          <path className={className} d={centerD} style={centerStyle} ></path>
          // <path d={outerD} style={outerStyle} ></path>
        }
      </g>
    )
  }
}


