import YouTubePlayer from 'youtube-player';
import getCaptions from './getCaptions.js';
import captionsJSON from './captionsStatic.js';

export default class videoPlayer {
	constructor(obj){
		this.id = obj.id;
		this.title = obj.title;
		this.description = obj.description;
		this.parts = obj.parts;
		this.state = {playerState: null};
		this.container = document.getElementById('vid'+ obj.id)
		this.player = null;
		this.done =  false;
		this.seekToSec = null;
		this.firstLoad = true;
		this.mobileControls = window.screen.width < 700;
	}
  get property() {}
  set property(value) {}
  play(id, time){
		this.labnolIframe(id, evt.target)
  }
 //  getIframe(id) {
 //    var iframe = document.createElement("iframe");
 //    iframe.setAttribute("src", "https://www.youtube.com/embed/" + id + "?autoplay=1&autohide=2&border=0&wmode=opaque&enablejsapi=1&controls=0&showinfo=0");
 //    iframe.setAttribute("frameborder", "0");
 //    iframe.setAttribute("id", "youtube-iframe");
 //    iframe.setAttribute("width", "560px");
 //    iframe.setAttribute("height", "315px");
 //    return iframe
	// }
	// getControls(){
	// 	const controlsDiv = document.createElement("div");
	// 	controlsDiv.className = "controls-ctr";

	// 	return controlsDiv
	// }
	play(){
		if (this.player) {
			this.controlsDiv.style.display = 'none'
			if (this.seekToSec) {
				this.player.seekTo(parseInt(this.seekToSec), true)
				this.seekToSec = null;
			}
	  	this.player.playVideo();
	  } else {
			this.create()
		}
	}
	pause(){
		this.player.pauseVideo();
	}
	seekTo(time){
		this.player.seekTo(parseInt(time), true);
	}
	mouseout(){
		this.partTitleNode.nodeValue="";
		this.partDescNode.nodeValue="";
		this.partDetailsDiv.style.display = 'none'
		if (this.seekToSec) {
			this.player.seekTo(parseInt(this.seekToSec), true)
		}

	}
	mouseover(part){
		this.partDetailsDiv.style.display = 'inline'

		this.partTitleNode.nodeValue=part.title;
		// this.partDescNode.nodeValue =part.description;
	}
	playAt(time){
		// console.log('start player at time', time);
		this.seekToSec = time;
		if (this.player) {
			// this.player.seekTo(time, true)
			this.play()
		} else {
			this.create()
		}
		// this.play()
	}
	launchMobCon(){
		function prettifyMinutes(time) {
			time = parseInt(time)
			let minutes = Math.floor(time / 60);
			let seconds = time - minutes * 60;

			if (minutes.toString().length === 1) {
				minutes = "0" + minutes;
			}
			if (seconds.toString().length === 1) {
				seconds = "0" + seconds;
			}
			return `${minutes}:${seconds}`;
		}
		let list =  document.createElement("ul");
		list.style.cssText = "position: absolute; height: 100%; width: 100%;";
		list.setAttribute("class", "mobile-list")

		for (var part of this.parts) {
			let li = document.createElement("li");
			li.setAttribute("class", "flex")

			let left = document.createElement("div")
			left.setAttribute("class", "width10")

			let right = document.createElement("div")
			right.setAttribute("class", "width90")


			let title = document.createTextNode(part.title);
			let time = document.createTextNode(prettifyMinutes(part.start));

			left.appendChild(time)
			right.appendChild(title)

			li.appendChild(left);
			li.appendChild(right);
			li.addEventListener('click', this.playAt.bind(this, part.start), false)
		  list.appendChild(li);
		}
		
		this.controlsContainer.innerHTML = "";
		this.controlsContainer.appendChild(list);
		// console.log(this.controlsContainer);
	}
	getMobControlsHtml(){
		let div = document.createElement("div");
		div.setAttribute("id", `con${this.id}`);
		div.style.cssText = "position: absolute; height: 100%; width: 100%; display: flex";
		div.setAttribute("class", "controls-div mobile")

		this.controlsContainer = div

		let button = document.createElement("button");
		button.addEventListener('click', () => {this.launchMobCon()}, false)
		button.setAttribute("class", "mob-button");
		let buttonText =  document.createTextNode("Launch Controls")

		button.appendChild(buttonText);
		div.appendChild(button);
		return div;
	}
	getControlsHtml(){
		// let list =  document.createElement("ul");
		// for (var part of this.parts) {
		// 	let li = document.createElement("li");
		// 	let node = document.createTextNode(part.title);
		// 	li.appendChild(node);
		// 	li.addEventListener('click', this.playAt.bind(this, part.start), false)
		// 	li.addEventListener('mouseover', this.seekTo.bind(this, part.start), false)
		//   list.appendChild(li);
		// }

		let controlsContainer = document.createElement("div");
		controlsContainer.style.cssText = "position: absolute; height: 100%; width: 100%; display: flex";
		controlsContainer.setAttribute("id", `con${this.id}`);
		controlsContainer.setAttribute("class", "controls-div")
		this.controlsContainer = controlsContainer
		
		let leftSide = document.createElement("div");
		leftSide.style.cssText = "width:66%";

		let playElement = document.createElement("h2");
		let playElementText = document.createTextNode("Play Video");
		playElement.appendChild(playElementText);
		playElement.addEventListener('click', () => this.play(), false)

		let titleElement = document.createElement("h3");
		let titleElementText = document.createTextNode(this.title);
		titleElement.appendChild(titleElementText);

		let descriptionElement = document.createElement("h3");
		let descriptionElementText = document.createTextNode(this.description);
		descriptionElement.appendChild(descriptionElementText);

		leftSide.appendChild(playElement)
		leftSide.appendChild(titleElement)
		leftSide.appendChild(descriptionElement)


		function polarToCartesian(centerX, centerY, radius, angleInDegrees) {
		  var angleInRadians = (angleInDegrees-90) * Math.PI / 180.0;

		  return {
		    x: centerX + (radius * Math.cos(angleInRadians)),
		    y: centerY + (radius * Math.sin(angleInRadians))
		  };
		}

		function describeArc(x, y, radius, startAngle, endAngle){

		    var start = polarToCartesian(x, y, radius, endAngle);
		    var end = polarToCartesian(x, y, radius, startAngle);

		    var arcSweep = endAngle - startAngle <= 180 ? "0" : "1";

		    var d = [
		        "M", start.x, start.y, 
		        "A", radius, radius, 0, arcSweep, 0, end.x, end.y
		    ].join(" ");

		    return d;       
		}

		// console.log("CONTAINER", this.container.width);

		const width = this.container.getAttribute('width');
		const height = this.container.getAttribute('height');
		// debugger;
		console.log(height);



		let svgElement = document.createElementNS('http://www.w3.org/2000/svg','svg');
		// svgElement.setAttribute("position", "fixed");
		svgElement.setAttribute("width", width);
		svgElement.setAttribute("heigth", height);
		svgElement.style.cssText = "width:" + width;
		svgElement.style.cssText = "height:" + height;

				 // <polygon points="200,100 300,200 200,300" style="fill:white;stroke:red;stroke-width:3" />

		let centerw = parseInt(width);
		let centerh = parseInt(height);
		let playButton = document.createElementNS("http://www.w3.org/2000/svg", "polygon");
		playButton.setAttribute("points", `${centerw/2-10},${centerh/2-40} ${centerw/2+30},${centerh/2+0} ${centerw/2-10},${centerh/2+40} `);
		playButton.style.cssText="fill:white;stroke:white;stroke-width:1"
		playButton.addEventListener('click', () => this.play(), false)
		svgElement.appendChild(playButton)
		// <polygon points="70, 55 70, 145 145, 100" fill="#fff"></polygon>


		let startDegrees = 0;
		let index = 1;
		for (var part of this.parts) {
			const partsNo = this.parts.length;
			let degrees = 360/partsNo;
			let start = startDegrees + 2;
			let end = (degrees * index) - 2;
			// console.log("start", start);
			// console.log("end", end);
			let pathElement = document.createElementNS("http://www.w3.org/2000/svg", "path");
			pathElement.setAttribute("d", describeArc(parseInt(width)/2, parseInt(height)/2, (parseInt(height)/4)- 10, start, end));
			pathElement.setAttribute("stroke","#F3FDFF")
			pathElement.setAttribute("stroke-width","8")
			pathElement.setAttribute("fill", "none")
			pathElement.addEventListener('click', () => {console.log("here")}, false)

			pathElement.addEventListener('click', this.playAt.bind(this, part.start), false)
			pathElement.addEventListener('mouseover', this.seekTo.bind(this, part.start), false)
			pathElement.addEventListener('mouseover', this.mouseover.bind(this, part), false)
			pathElement.addEventListener('mouseout', this.mouseout.bind(this, part.start), false)
			svgElement.appendChild(pathElement)

			index ++
			startDegrees += degrees;
		}

			let pathElement = document.createElementNS("http://www.w3.org/2000/svg", "path");
			pathElement.setAttribute("d", describeArc(parseInt(width)/2, parseInt(height)/2, (parseInt(height)/4), 0, 359.999));
			pathElement.setAttribute("stroke","#F3FDFF")
			pathElement.setAttribute("stroke-width","5")
			pathElement.setAttribute("opacity", 0.5)
			pathElement.setAttribute("fill", "none")
			svgElement.appendChild(pathElement)

			pathElement = document.createElementNS("http://www.w3.org/2000/svg", "path");
			pathElement.setAttribute("d", describeArc(parseInt(width)/2, parseInt(height)/2, (parseInt(height)/4)+10, 0, 359.999));
			pathElement.setAttribute("stroke","#F3FDFF")
			pathElement.setAttribute("stroke-width","5")
			pathElement.setAttribute("opacity", 0.9)
			pathElement.setAttribute("fill", "none")
			svgElement.appendChild(pathElement)


		// let pathElement = document.createElementNS("http://www.w3.org/2000/svg", "path");
		// pathElement.setAttribute("d", describeArc(200, 200, 170, 0, 85));
		// pathElement.setAttribute("stroke","#446688")
		// pathElement.setAttribute("stroke-width","20")
		// pathElement.setAttribute("fill", "none")
		// pathElement.addEventListener('click', () => {console.log("here")}, false)

		// let pathElement2nd = document.createElementNS("http://www.w3.org/2000/svg", "path");
		// pathElement2nd.setAttribute("d", describeArc(200, 200, 170, 95, 180));
		// pathElement2nd.setAttribute("stroke","#9CC3EA")
		// pathElement2nd.setAttribute("stroke-width","20")
		// pathElement2nd.setAttribute("fill", "none")

		// svgElement.appendChild(pathElement)
		// svgElement.appendChild(pathElement2nd)


		let rightSide = document.createElement("div");
		// rightSide.style.cssText = "width:33%";

		// rightSide.appendChild(list)

		this.progressBar = document.createElement("div")
		this.progressBar.setAttribute('class', "progress-bar")
		this.progress = document.createElement("div")

		this.progressBar.appendChild(this.progress)

		// rightSide.appendChild(svgElement)
		// rightSide.appendChild(progressBar)

		// controlsContainer.appendChild(leftSide)
		// controlsContainer.appendChild(rightSide)
		this.partDetailsDiv = document.createElement("div");
		this.partDetailsDiv.className = 'part-details container';
		let titleHeader = document.createElement("h4");
		let descriptionP = document.createElement("p");
		this.partTitleNode = document.createTextNode("");
		this.partDescNode = document.createTextNode("");
		titleHeader.appendChild(this.partTitleNode);
		descriptionP.appendChild(this.partDescNode);

		this.partDetailsDiv.appendChild(titleHeader);
		this.partDetailsDiv.appendChild(descriptionP);
		// this.partDetailsDiv.style.cssText = "diplay: none"
		this.partDetailsDiv.style.display = 'none'


		controlsContainer.appendChild(svgElement)
		controlsContainer.appendChild(this.partDetailsDiv)
		controlsContainer.appendChild(this.progressBar)
		return controlsContainer;
	}
	createControls(){
		let pContainer = this.container.parentElement;
		if (this.mobileControls) {
			pContainer.appendChild(this.getMobControlsHtml());
		} else {
			pContainer.appendChild(this.getControlsHtml());
		}

		this.controlsDiv = document.getElementById("con"+ this.id)
		this.create()
	}
	create() {
		// console.log("mobileControls", this.mobileControls);
		const element = document.getElementById('vid'+this.id)
		// const cElement = document.getElementById('con'+this.id)
		// const id = element.dataset.videoId;
		const width = element.getAttribute('width');
		const height = element.getAttribute('heigth');

    this.player = YouTubePlayer(element, {
          height: height,
          width: width,
          videoId: this.id,
          playerVars: {
          	controls: 0
          },
          // events: {
          //   'onReady': () => {this.onPlayerReady()},
          //   'onStateChange': (evt) => {this.onPlayerStateChange(evt)}
          // }
        });

		this.player.on('ready', (evt) => {
    	this.onPlayerReady()
		});		

		this.player.on('stateChange', (evt) => {
    	this.onPlayerStateChange(evt)
		});

	}

	onPlayerReady(event) {
	  this.captions = captionsJSON;
	}

	onPlayerStateChange(event) {
		// console.log(YT.PlayerState);
		// console.log(event.data);
		// console.log("this.stata.playerState", this.state.playerState)
		// console.log('event.data', event.data);
		console.log(YT.PlayerState);
		if(this.state.playerState == YT.PlayerState.PLAYING && event.data == YT.PlayerState.PAUSED) {
			console.log("SAVE THE SECONDS HERE",this.firstLoad);
			this.player.getCurrentTime().then((time) => {
				// console.log(pro-g);
				// console.log("this.duration", this.duration);
				// console.log("time", time);
				let percentage = (time/this.duration) * 100;
				console.log(`PROGRESS ${percentage}%`);
				this.progress.style.cssText = `width:${percentage}%`;

				this.seekToSec = time
	    });

		}
		let playerstate = ""
		switch (event.data) {
    case -1:
        playerstate = "UNSTARTED";
        break;
    case 0:
        playerstate = "ENDED";
        break;
    case 1:
        playerstate = "PLAYING";
        break;
    case 2:
        playerstate = "UNSTARTED";
        break;
    case 3:
        playerstate = "BUFFERING";
        break;
    case 5:
        playerstate = "CUED";
        break;
		default:
        playerstate = "not found"
		}
		// console.log("PLAYERSTATE", playerstate);
		this.player.getCurrentTime().then((time) => {
			// console.log(time);
    });
	  if (this.firstLoad && event.data == YT.PlayerState.PLAYING) {
	  	this.firstLoad = false;
	  	this.player.getDuration().then((time) => {
				this.duration = time;
    	});
	  	this.pause()
	  }
	  if (event.data == YT.PlayerState.PAUSED) {
	  	// console.log("update bar");
	  	this.controlsDiv.style.display = 'flex'
	  }


	  this.state.playerState = event.data
	}

};



