'use strict'
import React, {PropTypes} from 'react';
import VideoEmbedv2 from './VideoEmbedv2';
import PDFEmbed from './PDFEmbed';
import AudioEmbed from './AudioEmbed';
import WebEmbed from './WebEmbed';

// const mediaStatic = [{start: 5, end: 10, title: "PDF Lorem", type: "pdf", url: "https://www.research.ibm.com/software/IBMResearch/multimedia/Computing_Cognition_WhitePaper.pdf"}, {start: 15, end: 20, title: "Medium Post", newTab: true, type: "web", url: "https://medium.com/cognitivebusiness/tap-into-your-tribal-knowledge-48a3786df081"}, {start: 35, end: 40, title: "Podcast Lorem", type: "audio", url: "http://public.dhe.ibm.com/software/data/sw-library/cognos/podcasts/bi-radio/ep33-0_pd_ba-forum-ibm-cognos-10.mp3"}, {start: 55, end: 60, title: "Video Lorem", type: "video", src: "https://www.youtube.com/embed/VDjpPmnso94?list=PL4F1C783776E708A8"}, {start: 72, end: 77, title: "IBM site", type: "web", url: "http://www.ibm.com/analytics/us/en/technology/hadoop/hadoop-trials.html"}]

class Modal extends React.Component {
	constructor(props) {
    super(props);
    this.state = {
    	showMedia: null
    };
    this.showMedia = this.showMedia.bind(this);
    this.hideMedia =  this.hideMedia.bind(this);
    // this.showMediaClick = this.showMediaClick.bind(this);
    this.getContent = this.getContent.bind(this);
  }

  showMedia(media){
  	if(media.newTab) {
  		window.open(media.url, '_blank');
  	} else {
			this.setState({showMedia: media})
			this._modalContainer.scrollTop = 1000;
  	}
  }  
  // showMediaClick(index){
		// this.setState({showMedia: mediaStatic[index]})
  // }
	hideMedia(){
		this.setState({showMedia: null})
	}

  getContent(){
		if (this.state.showMedia) {
			switch (this.state.showMedia.type) {
	    case "pdf":
	        return (
					<div className="content-cover width100">
						<div className="width100 flex container" style={{fontSize: "1.5em", backgroundColor: "#0B0B0B"}}>
							<span className="flex pntr" onClick={this.hideMedia}>
								<i className="pntr" className="fa fa-arrow-left"/>
								<p style={{marginLeft: "5px"}}>go back</p>
							</span>						
						</div>
	        	<PDFEmbed source={this.state.showMedia.url} />
	        </div>
	        )
	        break; 
	    case "audio":
	        return (
					<div className="content-cover width100">
						<div className="width100 flex container" style={{fontSize: "1.5em", backgroundColor: "#0B0B0B"}}>
							<span className="flex pntr" onClick={this.hideMedia}>
								<i className="pntr" className="fa fa-arrow-left"/>
								<p style={{marginLeft: "5px"}}>go back</p>
							</span>						
						</div>
	        	<AudioEmbed sources={
							[
								{
									type: "mp3",
									url: this.state.showMedia.url,
								}
							]
						} />
					</div>
					)
	        break; 
	    case "web":
	        return (
					<div className="content-cover width100">
						<div className="width100 flex container" style={{fontSize: "1.5em", backgroundColor: "#0B0B0B"}}>
							<span className="flex pntr" onClick={this.hideMedia}>
								<i className="pntr" className="fa fa-arrow-left"/>
								<p style={{marginLeft: "5px"}}>go back</p>
							</span>						
						</div>
	        	<WebEmbed source={this.state.showMedia.url} />
	        </div>)
	        break; 
	    case "video":
	    		return (
	    			<div className="content-cover width100">
		    			<div className="width100 flex center" style={{backgroundColor: "#0D0E0E"}}>
								<div className="width100 flex container" style={{fontSize: "1.5em", backgroundColor: "#0B0B0B"}}>
									<span className="flex pntr" onClick={this.hideMedia}>
										<i className="pntr" className="fa fa-arrow-left"/>
										<p style={{marginLeft: "5px"}}>go back</p>
									</span>
								</div>
			    			<iframe width="560" height="315" src={this.state.showMedia.src} frameborder="0" allowfullscreen></iframe>
		        	</div>
	        	</div>
	    		)
	    		break;
	    default: 
	        return <Default />;
			}
		} else {
			return <Default />;
		}
  }

  render() {
  	const {hideModal} = this.props;
  	const content = this.getContent();
  	return (
			<div className="modal container" ref={(c) => this._modalContainer = c}>
				<div className="width100 flex" style={{flexDirection: "row-reverse", fontSize: "2em"}}>
					<i className="fa fa-times pntr" onClick={hideModal}/>
				</div>
				<div className="flex center width100" style={{marginBottom: "30px"}}>
					<VideoEmbedv2 
						data={{
				      id: 'GFZ2IaTVkY8',
				      title: '70 Miles From Shore with Watson: Woodside Energy and IBM',
				      description: 'Lorem ipsum dasknda dsalknd salkdn',
				      parts: [{title: "The Platform", description: "Big data at its finest; the complexity of this oil rig is just numbers unless captured in a way that pays off", start: "0", end: "15"},{title: "Cognitive truth", description: "Watson consumes every bit of data and provides insights that help this engine run its smoothest", start: "20", end: "24"},{title: "Watson, the Engineer", description: "We teach Watson to think like an engineer and, in turn Watson thinks like a thousand engineers", start: "60", end: "35"}],
				      media: [{start: 49, end: 55, title: "Watson: The platform for cognitive business", type: "video", src: "https://www.youtube.com/embed/VDjpPmnso94?list=PL4F1C783776E708A8"},{start: 55, end: 61, title: "See how Watson learns", type: "video", src: "https://www.youtube.com/embed/_Xcmh1LQB9I"}, {start: 15, end: 20, title: "Innovate with the cognitive cloud", type: "pdf", url: "https://public.dhe.ibm.com/common/ssi/ecm/xi/en/xim12350usen/XIM12350USEN.PDF"}, {start: 35, end: 40, title: "Cognitive business: The thinking business", type: "audio", url: "https://public.dhe.ibm.com/software/data/sw-library/cognos/podcasts/bi-radio/ep33-0_pd_ba-forum-ibm-cognos-10.mp3"}]
						}}
						height="385px"
				    width="640px"
				    showMedia={this.showMedia}
					/>
				</div>
				{
				// <div className="flex center container width100">
				// 	<p style={{cursor: "pointer", margin: "0 20px"}} onClick={this.showMediaClick.bind(null, 0)}>Lorem.pdf</p>
				// 	<p style={{cursor: "pointer", margin: "0 20px"}}><a style={{color: "inherit"}} target="_blank" href="https://medium.com/cognitivebusiness/tap-into-your-tribal-knowledge-48a3786df081">Medium Post</a></p>
				// 	<p style={{cursor: "pointer", margin: "0 20px"}} onClick={this.showMediaClick.bind(null, 2)}>Lorem.mp3</p>
				// 	<p style={{cursor: "pointer", margin: "0 20px"}} onClick={this.showMediaClick.bind(null, 3)}>Lorem.mp4</p>
				// 	<p style={{cursor: "pointer", margin: "0 20px"}} onClick={this.showMediaClick.bind(null, 4)}>Lorem.com</p>
				// </div>
				}
				
				<div className="container width100">
					{content}
				</div>
			</div>
    );
  }
}

Modal.propTypes = {
  
};


export default Modal;


const Default = props => (
	<span>
		<h4>How this was built</h4>
		<p>Woodside, an Australian oil and gas company, is partnering with IBM to create a cognitive system that will be trained by Woodside engineers, enabling users to surface evidence-weighted insights from large volumes of unstructured and historical data contained in project reports in seconds. Woodside developers employed the Watson Natural Language Classifier API to craft an architecture that lets engineers find the advice they need. The Watson Retrieve and Rank service then retrieves all relevant information from the corpus, ranks them in terms of relevance, and responds with the best matches, as well as related points of inspiration.</p>
		<h4>Business impact</h4>
		<p>That industryoffshore energyis as expensive as it is intensive. The stakesfrom human lives to environmental safety to shareholder resultsare incredibly high. Woodside uses IBMs Watson to unlock decades of the companys collective knowledge about projects. Woodside now has access to millions of files to help it's project development teams make decisions. Watson ingested tens of thousands of Woodside documents related to project development. Each typically more than 100 pages. This would normally take a human being, working 24 hours a day, more than five years to read. But Watson can produce meaningful answers in seconds.</p>
		<h4><a style={{color: "white"}}href="https://medium.com/@ibmcognitivebusiness/tap-into-your-tribal-knowledge-48a3786df081#.t8a1ysien" target="_blank"><i style={{marginRight: "5px"}} className="fa fa-sign-in" aria-hidden="true"></i>More Info</a></h4>
	</span>
)




